import React from 'react'
import classNames from 'classnames'
import '../App.css'

const RandomScreen = ({ randomTune, toggleSelected, setNewRandomTune }) => {
  const onClick = () => {
    toggleSelected(randomTune)
    setTimeout(() => {
      setNewRandomTune()
    }, 500)
  }

  if (randomTune) {
    return (
      <div className="Random-screen">
        <div 
          className={classNames({
            "Random-tune": true,
            "selected": randomTune.selected
          })}
          onClick={onClick}
        >
          <span className="Random-tune-title">
            {randomTune.title}
          </span>
          <span className="Random-tune-detail">
            {randomTune.composer} ({randomTune.year})
          </span>
        </div>
        <div className="Next-button" onClick={setNewRandomTune}>
          NEXT
        </div>
      </div>
    )
  }
  else return (
    <div className="Random-screen">
      <div className="Random-tune">
        <span className="Tune-detail">Loading random tune...</span>
      </div>
    </div>
  )
}

export default RandomScreen


