import React from 'react'
import classNames from 'classnames'
import '../App.css'

const SubmenuItem = ({ label, sortBy, setSortBy, filter, setFilter }) => {
  const onClick = () => {
    if (sortBy) setSortBy(label.toLowerCase())
    else setFilter(label.toLowerCase())
  }
  
  const getClassNames = () => {
    const current = sortBy ? sortBy : filter
    return classNames({
      'Submenu-item': true,
      'active': current === label.toLowerCase()
    })
  }
  
  const getLabel = () => {
    switch(label.toLowerCase()) {
      case 'jazz-tunes': return 'Jazz compositions'
      case 'rhythm':     return 'Rhythm changes'
      case 'holiday':    return 'Holiday songs'
      case 'waltz':      return 'Waltzes'
      case 'miles':      return 'Miles Davis'
      default:           return label
    }
  }

  return (
    <div className={getClassNames()} onClick={() => onClick()}>
      { getLabel() }
    </div>
  )
}

export default SubmenuItem


